import Vue from 'vue'
import { requestUserInfo, requestServiceTel } from '@/api/API'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'
import { MessageBox, Cell } from 'mint-ui'
Vue.component(Cell.name, Cell)

export default {
  name: 'profile',
  components: {
    Header, BottomBar
  },
  data () {
    return {
      serviceTel: '',
      user: {
        icon: '/static/img/profile/icon_cz@2x.png',
        nick_name: null,
        real_wallet: null
      }
    }
  },
  mounted () {
    this.getServiceTel()
    if (this.$store.state.User.token) {
      this.getUserInfo()
    }
  },
  methods: {
    async getUserInfo () {
      let res = await requestUserInfo(this.$store.state.User.token)
      if (res.code === 200) {
        this.user = Object.assign(this.user, res.data.user_info)
        this.$store.commit('User/updateUser', this.user)
      } else {
        window.EventBus.$emit('globalMessage', res.msg)
      }
    },
    async getServiceTel () {
      let res = await requestServiceTel(this.$store.state.User.token)
      if (res.code === 200) {
        this.serviceTel = res.data.tel
      }
    },
    goLogin () {
      if (this.$store.state.User.token === null) {
        this.$router.push('login')
      }
    },
    goRecharge () {
      this.$router.push('recharge')
    },
    goWithdraw () {
      this.$router.push('withdraw')
    },
    go (flag) {
      switch (flag) {
        case 1:
          this.$router.push('financialdetails')
          break
        case 2:
          this.$router.push('deallog')
          break
        case 3:
          this.$router.push('coopagree')
          break
        case 4:
          MessageBox.confirm(this.serviceTel, '是否拨打？').then(action => {
            if (action === 'confirm') {
              window.location.href = 'tel:' + this.serviceTel
            }
          }).catch(error => {
            console.log(error)
          })
          break
        case 5:
          this.$router.push('aboutus')
          break
        case 6:
          this.$router.push('generalize')
          break
        default:
          console.log('flag false')
          break
      }
    }
  }
}



